.heading {
  &:after {
    content: "";
    display: block;
    clear: both;
  }
  h1.title {
    font-size: 80px;
    font-weight: bold;
    margin: 0;
    display: block;
    float: left;
    span {
      color: $primaryColor;
    }
    @media only screen and (max-device-width: 480px) {
      font-size: 100px;
    }
  }

  .scores-container {
    float: right;
    text-align: right;

    .current-score {
      float: right;
      margin-left: 20px;

      &:after {
        content: "Time";
      }
    }
    .best-score:after {
      content: "Best Time";
    }
    .current-score,
    .best-score {
      position: relative;
      display: inline-block;
      background: $secondColor;
      padding: 15px;
      font-size: 25px;
      font-weight: bold;
      border-radius: $borderRadius;
      color: white;
      margin-top: 8px;
      text-align: center;
      width: 120px;

      &:after,
      &:after {
        position: absolute;
        width: 100%;
        top: 5px;
        left: 0;
        text-transform: uppercase;
        font-size: 13px;
        line-height: 13px;
        text-align: center;
        color: #eee4da;

        @media only screen and (max-device-width: 480px) {
          top: 15px;
          font-size: 25px;
        }
      }
      @media only screen and (max-device-width: 480px) {
        padding: 23px;
        font-size: 40px;
        width: 210px;
      }
    }
  }
}
.description {
  float: left;
  line-height: 42px;
  margin-bottom: 0;
  @media only screen and (max-device-width: 480px) {
    width: 55%;
    line-height: 45px;
    margin-top: 20px;
    font-size: 37px;
  }
}

.restart-button {
  display: none;
  background: $secondColor;
  border-radius: $borderRadius;
  padding: 0 20px;
  text-decoration: none;
  color: #f9f6f2;
  height: 40px;
  line-height: 42px;
  float: right;
  text-align: center;
  cursor: pointer;

  @media only screen and (max-device-width: 480px) {
    padding: 20px;
    width: 42%;
    height: 90px;
    cursor: pointer;
    margin-top: 20px;
    font-size: 40px;
  }
}
