.app-container {
  width: 100%;
  height: 570px;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  align-content: center;
  user-select: none;
  touch-action: none;
  text-align: center;

  .column {
    flex: 0 0 auto;
    width: auto;
    max-width: 100%;
  }
  @media only screen and (max-device-width: 480px) {
    height: 750px;
    margin: 60px 0;
  }

  .single-tile {
    background: $primaryColor;
    border-radius: $tilesBorderRadius;
    width: 100px;
    height: 100px;
    margin: 3px 2px 2px 3px;
    padding: 5px;
    font-size: 64px;
    color: #ffffff;
    cursor: pointer;
    text-align: center;
    &.darker {
      background: darken($primaryColor, 5%);
    }
    &.invisible {
      visibility: hidden;
    }
    @media only screen and (max-device-width: 480px) {
      width: 135px;
      height: 135px;
      margin: 4px 2px 2px 4px;
      font-size: 85px;
    }
  }
  button {
    padding: 15px 50px;
  }
  .start {
    background: $primaryColor;
    border-radius: $borderRadius;
    vertical-align: middle;
    font-size: 50px;
    color: #ffffff;
    text-align: center;

    @media only screen and (max-device-width: 480px) {
      font-size: 60px;
    }
  }

  #timer {
    font-size: 150px;
    text-align: center;
    vertical-align: middle;
    color: white;
    margin: 150px auto;
    @media only screen and (max-device-width: 480px) {
      font-size: 250px;
    }
  }

  .final-result {
    font-size: 50px;
    text-align: center;
    color: #fff;
    display: block;
    width: 100%;
    @media only screen and (max-device-width: 480px) {
      font-size: 80px;
    }
  }

  #restart-button {
    background: $primaryColor;
    border-radius: $borderRadius;
    font-size: 50px;
    color: #ffffff;
    text-align: center;
    margin: 50px;

    @media only screen and (max-device-width: 480px) {
      font-size: 70px;
    }
  }
}
