@import "./variables";
html,
body {
  margin: 0;
  padding: 0;
  background: $dark;
  color: $light;
  font-size: 18px;
  @media only screen and (max-device-width: 480px) {
    font-size: 30px;
  }
}

body {
  margin: 20px 0;
  @media only screen and (max-device-width: 480px) {
    margin: 40px 0;
  }
}
p {
  margin-top: 0;
  margin-bottom: 10px;
  line-height: 1.65;
}

a {
  font-weight: bold;
  cursor: pointer;
}
button {
  border: none !important;
}
div {
  display: block;
}

.container {
  max-width: 600px;
  margin: 0px auto;
  @media only screen and (max-device-width: 480px) {
    max-width: 800px !important;
  }
}

@import "./header";
@import "./game";

.footer {
  text-align: center;
}
